
/* eslint-disable @typescript-eslint/camelcase */
import { Options, setup, Vue } from 'vue-class-component';

import { ConfirmationInfoDto } from '@/types/models';

import { useConfirmationInfo, useCreateOrder } from '@/composable';

import { getDoctorFromBrowserStorage, getSpecialityFromBrowserStorage } from '@/utils';

import {
  RInput,
  RImg,
  RBtn,
  RSkeleton,
} from '@/ui/atoms';
import {
  RCard, RCardSection,
} from '@/ui/molecules';
import { DefaultCard } from '@/ui/organisms';

@Options({
  components: {
    RInput,
    RImg,
    RCard,
    RCardSection,
    RBtn,
    DefaultCard,
    RSkeleton,
  },
})
export default class UserData extends Vue {
  confirmationInfo = setup(useConfirmationInfo);

  newOrder = setup(useCreateOrder);

  get confirmationInfoData(): ConfirmationInfoDto | null {
    return this.confirmationInfo.confirmationInfo.data;
  }

  get date(): string {
    return this.confirmationInfoData?.interval.beginInterval
      ? this.$dateFilters.formatDate(this.confirmationInfoData.interval.beginInterval)
      : '';
  }

  get time(): string {
    return this.confirmationInfoData?.interval.beginInterval
      ? this.$dateFilters.formatTime(this.confirmationInfoData.interval.beginInterval)
      : '';
  }

  get price(): string {
    return this.confirmationInfoData?.product.price
      ? `${this.confirmationInfoData.product.price} ₽`
      : '';
  }

  get isDisableActionBtn(): boolean {
    return this.confirmationInfo.confirmationInfo.isLoading
      && !!this.confirmationInfo.confirmationInfo.error;
  }

  async payForService(): Promise<void> {
    this.$analyticsUtils.pushEvent('scheduled_go_to_pay', {
      eventLabel: getSpecialityFromBrowserStorage()?.specialityName ?? '',
      eventCategory: 'telemed',
      doctor_guid: getDoctorFromBrowserStorage()?.id ?? '',
    });

    this.newOrder.addNewOrder();
  }

  mounted(): void {
    this.$analyticsUtils.pushEvent('scheduled_confirmation', {
      eventLabel: getSpecialityFromBrowserStorage()?.specialityName ?? '',
      eventCategory: 'telemed',
      doctor_guid: getDoctorFromBrowserStorage()?.id ?? '',
    });
  }
}
